.SkillRect {
    width: 176px;
    height: 176px;
    background-color: #FFFFFF;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    margin-top: 20px;
    

    img{
        width: 30px;
        height: 30px;
        margin-left: 10px;
    }

    p{
        margin-left: 10px;
    }


}

@media only screen and (max-width: 768px){
    .SkillRect{
        width: 224px;
        height: 118px;
    }
}

@media only screen and (max-width: 600px){
    .SkillRect {
        width: 156px;
        height: 118px;
    }
}